  display: none;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 90rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.btn-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.5rem;
  display: inline-block;
  border: none;
}
.btn-amarillo:hover {
  background-color: #d17e08;
  cursor: pointer;
}

.btn-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.5rem;
  display: block;
  border: none;
}
.btn-amarillo-block:hover {
  background-color: #d17e08;
  cursor: pointer;
}

.btn-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.5rem;
  display: inline-block;
  border: none;
}
.btn-verde:hover {
  background-color: #67a200;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #e1e1e1;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode h5 {
  color: #FFFFFF !important;
}
.dark-mode span {
  color: #E08709 !important;
}
.dark-mode .form legend {
  color: #FFFFFF !important;
}
.dark-mode .form label {
  color: #e1e1e1 !important;
}
.dark-mode .form input:not([type=submit]), .dark-mode .form textarea, .dark-mode .form select {
  background-color: #333333 !important;
  color: #e1e1e1;
}
.dark-mode .form p {
  color: #FFFFFF !important;
}
.dark-mode .resumen .icono {
  filter: invert(100%);
}
.dark-mode .anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .icono {
  filter: invert(100%);
}
.dark-mode .precio {
  color: #71B100;
}

.footer {
  background-color: #333333;
  padding: 1rem;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .navbar {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  max-width: 60rem;
  margin-top: 3rem;
  font-size: 1.5rem;
  text-align: center;
  font-weight: 700;
  line-height: 1.5;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    margin: 0;
    font-size: 3.5rem;
    text-align: left;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .btn-darkmode {
  filter: invert(100%);
  width: 2.5rem;
  margin: 0;
  margin-bottom: 1rem;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .derecha .btn-darkmode {
    margin-bottom: 2rem;
  }
}
.derecha .btn-darkmode:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navbar {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-out, opacity 0.5s linear, height 0.4s linear;
  height: 0;
}
@media (min-width: 768px) {
  .navbar {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navbar a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
}
.navbar a:last-of-type {
  margin-bottom: 3rem;
}
.navbar a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navbar a {
    margin-right: 2rem;
  }
  .navbar a:last-of-type {
    margin-right: 0rem;
    margin-bottom: 0rem;
  }
}

.logo-nav {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .logo-nav {
    margin-bottom: 0;
  }
}

.menu-hamburguesa {
  display: block;
}
@media (min-width: 768px) {
  .menu-hamburguesa {
    display: none;
  }
}
.menu-hamburguesa img {
  width: 2rem;
}
.menu-hamburguesa:hover {
  cursor: pointer;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 8rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 480px) {
  .iconos-nosotros {
    column-gap: 3rem;
  }
}
.icono {
  padding: 2.5rem;
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}
.icono p {
  text-align: center;
  line-height: 1.2;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #cccccc;
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncios {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio h3 {
  font-weight: 400;
}
.anuncio p {
  font-weight: 300;
  line-height: 1.4;
  margin-bottom: 2rem;
}
.anuncio .precio {
  font-weight: 900;
}

.precio {
  color: #71B100;
  font-weight: 900;
  font-size: 3rem;
}

.iconos-info {
  max-width: 30rem;
  display: flex;
  list-style: none;
  margin-left: -6rem;
}
.iconos-info li {
  flex: 1;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
}
.iconos-info li img {
  flex: 0 0 3rem;
  margin-right: -1.3rem;
}
.iconos-info li p {
  font-weight: 900;
  margin-bottom: 0;
  margin-right: 2rem;
}

.no-webp .section-imgContacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .section-imgContacto {
  background-image: url("../img/encuentra.webp");
}

.section-imgContacto {
  background-position: center center;
  background-size: cover;
  padding: 20rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.section-imgContacto h2 {
  font-size: 4rem;
  margin: 0;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1;
}
.section-imgContacto p {
  margin: 0;
  margin-top: 7rem;
  font-size: 2rem;
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .seccion-blog {
    display: grid;
    grid-template-columns: 3fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 8rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.entrada a {
  color: #000000;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .entrada a {
    margin-top: 0;
  }
}
.entrada h4 {
  line-height: 1.4;
  margin: 0;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .entrada h4 {
    margin-top: 0;
  }
}
.entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.spandata span {
  color: #E08709;
}

.comentario {
  background-color: #71B100;
  font-size: 2rem;
  line-height: 1.5;
  font-weight: 300;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 3.5rem;
}
.comentario blockquote {
  padding-left: 3rem;
  position: relative;
}
.comentario blockquote::before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  width: 2rem;
  height: 3rem;
  display: block;
  position: absolute;
  left: 0;
  margin: 0;
}
.comentario p {
  text-align: right;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .nosotros-contenido {
    display: flex;
    align-items: center;
    column-gap: 4rem;
  }
  .nosotros-contenido img {
    width: 60rem;
  }
}
.nosotros-contenido h4 {
  color: #333333;
}

.sobrenosotros {
  margin-top: 7rem;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]), .form textarea, .form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 20rem;
}
.form .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}