@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-webp .inicio {
    background-image: url(../img/header.jpg);
}

.webp .inicio {
    background-image: url(../img/header.webp);
}

.header{
    background-color: v.$grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio{
        background-position: center center;
        background-size: cover;
        @include m.tablet {
            min-height:70rem;
        }

        .contenido-header{
            @include m.tablet {
                min-height:70rem;
            }
        
        }
    } 
}


.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
   
    h1{
        color: v.$blanco;
        max-width: 60rem;
        margin-top: 3rem;
        font-size: 1.5rem;
        text-align: center;
        font-weight: v.$bold;
        line-height: 1.5;
        margin: 0 auto;
    }


    @include m.tablet {
        h1{
            margin: 0;
            font-size: 3.5rem;
            text-align: left;
        }
      
        
    }
}

